import request from './request.js'

// 
export function listObj(data) {
  return request.myPro({
    url: '/business/Universal_anti_lead_reporting/list',
    method: 'get',
    data
  })
}

// 
export function getObj(id) {
  return request.myPro({
    url: '/business/Universal_anti_lead_reporting/' + id,
    method: 'get'
  })
}

// 
export function addObj(data) {
  return request.myPro({
    url: '/business/Universal_anti_lead_reporting',
    method: 'post',
   data
  })
}

// 
export function updateObj(data) {
  return request.myPro({
    url: '/business/Universal_anti_lead_reporting',
    method: 'put',
    data
  })
}

// 
export function delObj(id) {
  return request.myPro({
    url: '/business/Universal_anti_lead_reporting/' + id,
    method: 'delete'
  })
}
