import request from './request.js'


// 
export function registerByPhone(data) {
	return request.myPro({
		url: "/auth/registerByPhone",
		method: 'post',
		data
	})
};
// 
export function login(data) {
	return request.myPro({
		url: "/auth/login",
		method: 'post',
		data
	})
};
// 
export function loginByPhone(data) {
	return request.myPro({
		url: "/auth/loginByPhone",
		method: 'post',
		data,
	})
};

// 
export function getRole() {
	return request.myPro({
		url: "/business/myUniversalAntiUser/getRole",
		method: 'post',
	})
};