import Qs from './formatURL'

//
// export const ROOTHTTP = "https://www.czds-oa.com/prod-api" 


//
export const ROOTHTTP = "http://47.94.221.213:4380/prod-api"

// 
function myPro(options) {
	const csrfToken = uni.getStorageSync('token');
	var head = '';
	var httpUrl = ROOTHTTP
	if (csrfToken) {
		head = `Bearer ${csrfToken}`
		if (options.token != undefined && !options.token) {
			head = ''
		}
	} else {
		head = csrfToken
	};
	/*if (options.advertisement != undefined && options.advertisement) {
		httpUrl = ROOTHTTP
	} else if (options.isOA) {
		httpUrl = OAHTTP;
	} else if (options.isSTUDY) {
		httpUrl = STUDYHTTP;
	} else if (options.isOpen) {
		httpUrl = OPENHTTP;
	} else {
		httpUrl = BASEHTTP
	}*/


	//console.log(httpUrl);
	//  promise
	return new Promise((resolve, reject) => {
		wx.getNetworkType({
			success: res => {
				const networkType = res.networkType;
				if (networkType == "none") {
					wx.showToast({
						title: ',',
						icon: 'none', //,
						duration: 1500, //,
						mask: true, //,
						success: res => {}
					})
				} else if (networkType == "2g") {
					wx.showToast({
						title: '2g,',
						icon: 'none', //,
						duration: 1500, //,
						mask: true, //,
						success: res => {}
					})
				}
			}
		});
		if (options.method == 'get') {
			if (!options.url.includes('?') && options.data) {
				if (options.noGetData) {
					options.url = options.url;
				} else {
					options.url = options.url + '?' + Qs.stringify(options.data)
				}
				options.data = ''
			}
		}

		// 
		wx.request({
			url: httpUrl + options.url || 'url', //",
			data: options.data, //",
			data1: options.data1 || '', //",
			method: options.method || 'GET',
			dataType: options.dataType || 'json', //json JSON.parse
			timeout: 15000,
			header: {
				'content-type': 'application/json',
				'Authorization': head,
			} || {},
			// success: resolve,
			success: function(res) {
				// uni.hideToast();
				uni.hideLoading()
				if (res.data.code == 200) {
					resolve(res.data)
				} else if (res.data.code == 401) {
					uni.showToast({
						title: '!',
						icon: "none",
						mask: true
					});

					uni.setStorageSync('token', '')
					uni.redirectTo({
						url: '/pages/subMine/login'
					})
				} else if (res.data.code == 0) {
					resolve(res.data)
				} else {
					uni.showToast({
						title: res.data.msg || '',
						icon: "none",
						mask: true
					});
					reject(res.data)
				}
			},
			fail: function(err) {
				console.log(':', err);
				resolve(err)
			}
		});


	})
}

// 
export default {
	myPro,
	// myPro2,
	ROOTHTTP
};